body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: #e2e2e2;
}
.ball {
  height: 60px;
  width: 60px;
  background-color: slategrey;
  border-radius: 50%;
  padding: 32px;
}
#slides {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  border: 3px solid #333;
}

.box {
  margin: 32px;
  min-height: 400px;
  padding: 0 32px;
  min-width: 760px;
  -webkit-transition: opacity 0.9s cubic-bezier(0.645, 0.045, 0.355, 1),
    -webkit-transform 1s cubic-bezier(0.645, 0.045, 0.355, 1);
  transition: opacity 0.9s cubic-bezier(0.645, 0.045, 0.355, 1),
    -webkit-transform 1s cubic-bezier(0.645, 0.045, 0.355, 1);
  -o-transition: opacity 0.9s cubic-bezier(0.645, 0.045, 0.355, 1),
    transform 1s cubic-bezier(0.645, 0.045, 0.355, 1);
  transition: opacity 0.9s cubic-bezier(0.645, 0.045, 0.355, 1),
    transform 1s cubic-bezier(0.645, 0.045, 0.355, 1);
  transition: opacity 0.9s cubic-bezier(0.645, 0.045, 0.355, 1),
    transform 1s cubic-bezier(0.645, 0.045, 0.355, 1),
    -webkit-transform 1s cubic-bezier(0.645, 0.045, 0.355, 1);
}

h2 {
  color: #333;
  font-size: 22px;
  margin: 32px 0;
  font-weight: 300;
}



